import axios from 'axios';

const API_KEY = 'eade4640657bb63c2c171ffa3ce711eb';
const BASE_URL = 'https://api.themoviedb.org/3/';

const fetchGetTrending = () => {
  return axios.get(
    `${BASE_URL}trending/all/day?api_key=${API_KEY}&language=en-US`,
  );
};

const fetchMoviesByQuery = searchQuery => {
  return axios.get(
    `${BASE_URL}search/movie?api_key=${API_KEY}&language=en-US&query=${searchQuery}&page=1&include_adult=false`,
  );
};

const fetchMoviesDetails = movieId => {
  return axios.get(
    `${BASE_URL}movie/${movieId}?api_key=${API_KEY}&language=en-US`,
  );
};

const fetchMoviesCast = movieId => {
  return axios.get(
    `${BASE_URL}movie/${movieId}/credits?api_key=${API_KEY}&language=en-US`,
  );
};

const fetchMoviesReviews = movieId => {
  return axios.get(
    `${BASE_URL}movie/${movieId}/reviews?api_key=${API_KEY}&language=en-US`,
  );
};

export default {
  fetchGetTrending,
  fetchMoviesByQuery,
  fetchMoviesDetails,
  fetchMoviesCast,
  fetchMoviesReviews,
};
